.allgraphicscontainer{
    display: flex;
    padding-top: 60px;
    justify-content: center;
    align-items: center;
    padding-bottom: 60px;
    background-color: #DFE6EF;
    margin-top: 60px;
}

.galleryWrap2 {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    max-width: 620px;
    margin: 0 auto;
  }
  
  .galleryWrap2 .single2 {
    width: 200px;
    cursor: pointer;
  }
  
  .galleryWrap2 .single2 img {
    max-width: 100%;
    border-radius: 10px;
    /* background: url(<path-to-image>), lightgray 50% / cover no-repeat; */
    box-shadow: 5px 6px 4px 0px rgba(0, 0, 0, 0.6);
  }
  
  .galleryWrap2 .single2 img:hover {
    transform: scale(1.02);
  }
  
  .sliderWrap2 {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  
  .sliderWrap2 .btnClose2, .sliderWrap2 .btnPrev2, .sliderWrap2 .btnNext2 {
    position: fixed;
    cursor: pointer;
    /* opacity: 0.9; */
    color: #fff;
    z-index: 9999;
    width: 3rem;
    height: 3rem;
  }
  
  .btnNext2:hover, .btnPrev2:hover, .btnClose2:hover {
    opacity: 1;
  }
  
  .sliderWrap2 .btnClose2 {
    top: 40px;
    right: 40px;
  }
  
  .sliderWrap2 .btnPrev2 {
    top: 50%;
    transform: translateY(-50%);
    left: 40px;
  }
  
  .sliderWrap2 .btnNext2 {
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
  }
  
  .fullScreenImage2 {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .fullScreenImage2 img {
    max-width: 100%;
    max-height: 100%;
    pointer-events: none;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */  
  }

  



  


  @media screen and (max-width: 2560px){

  .galleryWrap2 {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    align-items: center;
    justify-content: center;
    max-width: 1152px;
    margin: 0 auto;
  }


  .galleryWrap2 .single2 {
    width: 213px;
    cursor: pointer;
  }
}

@media screen and (max-width: 1050px){
  .allgraphicscontainer{

    padding: 0 4rem 0;
  }
  
  
  .galleryWrap2 .single2 {
    width: 200px;
  }

  .allgraphicscontainer{
    padding-top: 30px;
  
  
  }
}


@media screen and (max-width: 768px){

  .galleryWrap2 {
    gap: 10px;
  }



  .galleryWrap2 .single2 {
    width: 150px;
  }

  .allgraphicscontainer{
  padding-top: 30px;


}

}

@media screen and (max-width: 700px){

  .galleryWrap2 .single2 {
    width: 135px;
  }



}

@media screen and (max-width: 550px){

  .allgraphicscontainer{

    padding: 0 1rem 0;
  }


  .galleryWrap2 .single2 {
    width: 48.2%;
  }

  .allgraphicscontainer{
    padding-top: 30px;
  
  
  }

}