.galleryWrap {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    max-width: 620px;
    margin: 0 auto;
  }
  
  .galleryWrap .single {
    width: 200px;
    cursor: pointer;
  }
  
  .galleryWrap .single img {
    max-width: 100%;
    border-radius: 10px;
    /* background: url(<path-to-image>), lightgray 50% / cover no-repeat; */
    box-shadow: 5px 6px 4px 0px rgba(0, 0, 0, 0.6);
  }
  
  .galleryWrap .single img:hover {
    transform: scale(1.02);
  }
  
  .sliderWrap {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  
  .sliderWrap .btnClose, .sliderWrap .btnPrev, .sliderWrap .btnNext {
    position: fixed;
    cursor: pointer;
    opacity: 0.9;
    color: #fff;
    z-index: 9999;
    width: 3rem;
    height: 3rem;
  }
  
  .btnNext:hover, .btnPrev:hover, .btnClose:hover {
    opacity: 1;
  }
  
  .sliderWrap .btnClose {
    top: 40px;
    right: 40px;
  }
  
  .sliderWrap .btnPrev {
    top: 50%;
    transform: translateY(-50%);
    left: 40px;
  }
  
  .sliderWrap .btnNext {
    top: 50%;
    transform: translateY(-50%);
    right: 40px;
  }
  
  .fullScreenImage {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .fullScreenImage img {
    max-width: 100%;
    max-height: 100%;
    pointer-events: none;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */  
  }

  .textheader{
    display: none;
    align-items: center;
  }

  .textheader h1{
    text-align: center;
  }

 

  @media screen and (max-width: 2560px){
    
    .allportfoliocontainer{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 90px;
    }
    .portfoliocontainer{
       
        
        align-items: center;
        justify-content: center;
        max-width: 1152px;
       
    }
    
    
    
    .galleryWrap {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        align-items: center;
        justify-content: center;
        max-width: 1152px;
        margin: 0 auto;
      }


      .galleryWrap .single {
        width: 270px;
        cursor: pointer;
      }


      .PortfolioHeader img{
        filter: drop-shadow(0px 9px 4px rgba(0, 0, 0, 0.25));
        height: 90px;  
      }

      .PortfolioHeader{
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 30px;
        
    }   
       
    }


    @media screen and (max-width: 1050px){
      .allportfoliocontainer{

        padding: 0 4rem 0;
      }
      .PortfolioHeader img{
        width: 600px;
        height: 70px;
      }
      .galleryWrap .single {
        width: 209px;
      }
    }


    @media screen and (max-width: 768px){
      .galleryWrap .single {
        width: 206px;
      }

      .galleryWrap{
        gap: 10px;
      }

    }
    @media screen and (max-width: 768px){

      .PortfolioHeader img{
        width: 600px;
        height: 70px;
      }

    }
    @media screen and (max-width: 700px){

      .galleryWrap .single {
        width: 215px;
      }

    }
    
    
    
    @media screen and (max-width: 550px){
        .PortfolioHeader img{
         display: none;
          width: 350px;
           height: 45px;
      }

      .galleryWrap .single {
       width: 100%;
      }
    
    
      .allportfoliocontainer{

        padding: 0 1rem 0;
      }
      .text{
        padding-bottom: 20px;
      }
    

      .textheader{
        
        display: block;
        padding-bottom: 20px;
        color: #0015CE;
      }

      .textheader h1{
        font-size: 18px;
      }
    
      .text h1{
        
        font-size: medium;
        text-align: center;
      }

    }