  .slick-list, .slick-slider, .slick-track{
    padding: 2rem 0;
}


.center .slick-center .project {
    transform: scale(1.3);
    z-index: 10000;
    transition: all 400ms ease-in-out;
    opacity: 1;
    pointer-events: all;
    

}

.center .project{
    opacity: 0.7;
    pointer-events: none;
}


@media screen and (max-width: 990px){
   
    .center .slick-center .project {
    transform: scale(1);
    
    }

    .center .project{
        opacity: 1;
        pointer-events: all;
    }

}
 
  
@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }
    .section__padding {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }
    .section__padding {
        margin: 4rem 2rem;
    }
}