




.eduimages{
display: flex;
justify-content: center;
align-items: center;

flex-direction: row;
align-self: center;

/* height: 547px; */


padding-top: 36px;

}

.eduimages img{
    align-self: center;
    max-width:  1152px;
    
}

.Eduheader{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 90px;
    
}

.Eduheader img{
    height: 90px;  
    filter: drop-shadow(0px 9px 4px rgba(0, 0, 0, 0.25));
   
}

@media screen and (max-width: 2560px){
.educationcontainer{
    display: none;
}
.eduimages{
    gap: 71px;
}



}




@media screen and (max-width: 1050px){
   
   .educationcontainer{
    display: block;
    padding-left: 4rem;
    padding-right: 4rem;
   }

   .educationcontainer2{
    display: none;
   }
   
    .eduimages{
        display: flex;
        flex-direction: row;    
    }

    .Eduimages{

   display: none;
   
    }

}




@media screen and (max-width: 700px){

    .Eduheader img{
     
       width: 500px;
   
           
   }


}

@media screen and (max-width: 550px) {
   
   .educationcontainer{
       
   padding: 0rem 1rem;
   }

   .Eduheader img{
      
        width: 100%;  
        height: 40px;    
    }





}