.allconts{
    display: flex;
    justify-content: center;
    align-items: center;
}

.mentorheadercontainer h1{
    padding-top: 30px;
    text-align: center;
}

.mentorheadertext{
    color: #0015ce;
    display: none;
}



.mentorheadercontainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 90px;
    width: 1152px;
}

.mentorheader img{
   display: flex;
    height: 90px;
    filter: drop-shadow(0px 9px 4px rgba(0, 0, 0, 0.25));
    align-self: center;
    
}



@media screen and (max-width: 1050px){
   
   .mentorheadercontainer{
    
      flex-direction: column;
       padding: 0rem 4rem;
          
      }
   
    .mentorheader img {
        width: 600px;
        height: 70px;
       
     }

}

@media screen and (max-width: 700px){

    .mentorheader img {
     
       width: 450px;
       height: 55px;
      
   
           
   }


}

@media screen and (max-width: 550px) {
    .mentorheadercontainer{
        
        padding: 2rem 1rem 0rem;
        
       
        }

        .mentorheadertext{
            display: block;
        }
  

    .mentorheader {
        display: none;

      
            
     }
     .mentorheadertext h1{
        font-size: 18px;
     }

    
.mentortext h1{
    font-size: 16px;
    text-align: center;
}




    }