.all{
     position: relative; 
     perspective: 100rem;
     overflow: hidden;
     /* margin-bottom: 90px; */
     
}
 
 
 
 .headerback{
    background: url(../../Images/bgs1.svg) lightgray 50%  / cover no-repeat ;
    background-repeat: no-repeat;
background-position: center center;
/* background-attachment: fixed; */
background-size: cover;
    position: absolute;
    width: 100%;
    height: 100vh;
   z-index: 0;
   object-fit: cover;
  opacity: 0.3;
   animation: scale 25s;
  }  


  @keyframes scale {
    0% {
      transform: scale(1.3);
    }
    100% {
      transform: scale(1);
    }
  }


  .t{
    
    display: flex;
    position: relative;
    z-index: 1; 
    width: 100%;

    justify-content: center;
    align-items: center;

}




.headercontainer{
    
    display: flex;
    height: 729;
    width: 1152px;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0;
    padding-top: 112px;
    
    
    
}
   
.titlecontainer 
{
    
font-family: "Poppins" ,sans-serif;
/* font-weight:800; */
margin-bottom: 0;
margin-top: 0;

}


.pwhite{
    color: black ;
    opacity: 0;
    animation: moveBanner 1s 0.8s forwards;
}

.titlecontainer p
{
  position: relative; 
    /* color: #000; */
font-size: 18px;
font-family: "Raleway" ,sans-serif;
font-weight: 600;
line-height: 120%;
letter-spacing: -0.36px;
opacity: 0;
animation: moveBanner 1s 0.5s forwards;
z-index: 1;
margin-bottom: auto;
}

.titlecontainer h1{

    color: black;
    font-family: "Raleway", sans-serif;
    font-size: 60px;
    margin-bottom: 0px;
    margin-top: 0px;
    font-weight:800;
    opacity: 0;
    animation: moveBanner 1s 0.7s forwards;
    }
    

     

.herobtn button{
    padding: 16px 20px;
    border-radius: 24px;
    border: 0;
    outline: none;
    background: #0015CE;
    /* border: 1px solid #000; */
    color: #fff;
    display: inline-block;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    text-shadow: 0 0.2rem 0.4rem rgba(0, 0, 0, 0.2);
  
  cursor: pointer;

    
    
}

.herobtn button:hover{
  background: #0012b1;
}

.herobtn{
    display: flex;
    margin-top: 23px;
    gap: 30px;
     width: 565px;
     opacity: 0;
     animation: moveBanner 1s 0.9s forwards;
}


@keyframes moveBanner {
    0% {
      transform: translateY(40rem) rotateY(-20deg);
    }
    100% {
      transform: translateY(0) rotateY(0);
      opacity: 1;
    }
  }


.imagecontainer{

    align-self: flex-end;
    
 }

 .imagecontainer img{
     vertical-align: middle;
     position: relative;
     bottom: 10px;
     width: 430px;

    
 }

 .imagecontainer2 img{
  display: none;
}
.imagecontainer2{
  display: none;
}

@media screen and (max-width: 1050px){
  .headercontainer{
    flex-direction: column;
    padding: 5rem 4rem 3rem;
  }

  h2{
    white-space: pre-wrap;
  }

  .imagecontainer {
  display: none;
    
  }


  .imagecontainer2{
   display: flex;
   align-self: center;
   object-position: top;
 }

  .imagecontainer2 img{
   display: flex;
   position: relative;
   bottom: 200px;
   height: 518px;
   width: 700px;
    
  }


  .titlecontainer{
    position: relative;
    top: 480px;
    text-align: center;
  }
  .herobtn{
    display: flex;
    justify-content: center;
    align-items: center;
  }

}

 @media screen and (max-width: 700px) {
    
  .headercontainer{
      padding: 4rem 0rem 2rem;
     
  }

  .titlecontainer h1{
    font-size: 45px;
  }

  .titlecontainer p{
    font-size: 16px;
  }

  .imagecontainer2 img{
    height: 418px;
    width: 580px;
    bottom: 90px;
  }

  .titlecontainer{
    top: 450px;
  }

}

@media screen and (max-width: 550px){
  .headercontainer{
    padding: 3rem 0rem 6rem;
  }

 
  .imagecontainer2 img{
    height: 258px;
    width: 360px;
    bottom: 90px;
  }
 
  /* .titlecontainer h1{
    font-size: 25px;
  }*/

  .titlecontainer p{
    font-size: 16px;
    white-space: pre;
    font-weight: 550;
    
  } 

  .titlecontainer h1{
    font-size: 30px;
  }

  

  .titlecontainer{
    top: 310px;
  }
   

  .herobtn button{
    padding: 16px 20px;
    border-radius: 24px;
    background: #0015CE;
    color: #fff;
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    
  }
.pwhite h2{
font-size: 14px;
}



}


