.menu__navbar{

    
display: flex;

width: 100%;
height: 92px;
justify-content: center;
align-items: center;
/* gap: 10px; */
/* opacity: 0.800000011920929; */
background: transparent;
position: fixed;
z-index: 2;


}

.menu__navbar.active{
    background: #fff;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
    transition: 0.5s ease-in-out;
}


.menu__navbar-links{
display: flex;
width: 1152px;
justify-content: space-between;
align-items: center;

height: 92px;

}

.navlinkscontainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 60px;
    /* display: inline-block;
  position: relative; */
 
}

p a{
    position: relative;
    color: #000000;
    text-decoration: none;
}

.navbarmenucontainerlinks a{
    color: #fff;
}


 p a::after{
    content: "";
    position: absolute;
    background-color: #0015CE;
    height: 3px;
    width: 0;
    left: 0;
    bottom: -3px;
   
}

 p a:hover::after{
    width: 100%;
    
}
    




.navlinkscontainer p{
    /* color: #000; */
font-size: 14px;
font-family: "Poppins", sans-serif;
font-weight: 500;
line-height: normal;
 margin: 0 0rem;
 cursor: pointer;
 /* text-decoration: underline; */
}



.menu__navbar-links__logo img{
    
    width: 260px;
    /* align-self: flex-end;  */
 
}

.menu__navbar-links__logo{
display: flex;
    /* padding-left: 5rem; */
    position: relative;
    right: 20px;
    /* bottom: 15px; */
}    




.navbarmenu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.navbarmenu svg{
    cursor: pointer;
}

.navbarmenucontainer{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: #0015CE;
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.navbarmenucontainer p {
margin: 1rem 0;
}

@media screen and (max-width: 1050px) {
    
    
    
    
    .navlinkscontainer{
        display: none;
        /* gap: 0px; */
    }

    .navbarmenucontainerlinks p{
        color: #fff;
        font-size: 14px;
font-family: "Poppins", sans-serif;
font-weight: 500;
    }

    .navbarmenu{
        display: flex;
    }



    .menu__navbar{
        padding: 2rem 4rem;
       
    }
}


@media screen and (max-width: 700px) {
    
   .menu__navbar{
       padding: 2rem 2rem;
      
   }
}


@media screen and (max-width: 550px) {
    
    .menu__navbar{
       display: flex;
       position: fixed;
       
        padding: 1.5rem ;
        padding-top: 2rem;
        height: 40px;
        background: #fff;
        
       
    }

    .navbarmenucontainer{
        top: 20px;
    }
    
 


    .menu__navbar-links__logo img{
        width: 180px; 
         
    }

}




 


