.AboutMeHeader{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 90px;
    
    
}

.AboutMeHeader img{
   width: 600px;
    /* height: 90px; */
    filter: drop-shadow(0px 9px 4px rgba(0, 0, 0, 0.25));
   
   
}

.Aboutmeheadertext h1{
    display: none;
}

.AboutMeText{
    display: flex;
    align-items: center;
justify-content: center;

padding: 20px 0px;
flex-direction: column;

gap: 38px
}

.AboutMeText p{
white-space: pre-wrap;
color: #000;
text-align: justify;
font-family: "Raleway", sans-serif;
font-size: 18px;
font-weight: 500;
line-height: 199.5%;
letter-spacing: -0.4px;
max-width: 1152px
}

.long-text{
    max-height: 0;
    overflow: hidden;
    display: inline-block;
    transition: max-height 0.3s;
    
}

.expanded{
    max-height: 50rem;
    transition: max-height 2s;
    
}



.AboutMeText button {
background: none;
margin-top: 0;
margin-bottom: 0;

border: 0;

outline: none;
color: blue;
font-size: 15px;
cursor: pointer;
}

@media screen and (max-width: 1050px){
    .allcontainer{
      flex-direction: column;
     padding: 0rem 4rem;
        
    }

    .AboutMeText p{

    font-size: 16px;
 
    }
     .AboutMeText button{
        
         position: relative;
         
         /* left: 800px; */
         /* margin-right: 4rem;  */
     }

     .AboutMeHeader img {
        width: 600px;
        /* height: 70px; */
       
     }


    }

@media screen and (max-width: 700px){

     .AboutMeHeader img{
      
        width: 450px;
        /* height: 55px; */
       
    
            
    }


 }

@media screen and (max-width: 550px) {
    
    .Aboutmeheadertext h1{
        font-size: 18px;
        display: block;
        color: #0015CE;
        text-align: center;
    }

    
    
    .allcontainer{
        
    padding: 0rem 1rem 2rem;
   
    }

    .AboutMeHeader img{
display: none;
        width: 350px;
        /* height: 45px; */
            
     }

     .AboutMeHeader{
        padding-top: 50px;
     }
     



    }